import axios from 'axios';

const KEY = 'f34ed5891c665d5a7f0c87dfa3d7e171';

//---- Ð—Ð°Ð¿Ñ€Ð¾Ñ ÑÐ°Ð¼Ñ‹Ñ… Ð¿Ð¾Ð¿ÑƒÐ»ÑÑ€Ð½Ñ‹Ñ… Ñ„Ð¸Ð»ÑŒÐ¼Ð¾Ð² Ð½Ð° ÑÐµÐ³Ð¾Ð´Ð½Ñ ----
export const fetchTrendingMovies = async () => {
  const response = await axios.get(
    'https://api.themoviedb.org/3/trending/all/day',
    {
      params: {
        api_key: KEY,
      },
    }
  );
  return response.data;
};

//---- Ð—Ð°Ð¿Ñ€Ð¾Ñ Ð¿Ð¾ Ð¿Ð¾Ð¸ÑÐºÑƒ ÐºÐ¸Ð½Ð¾Ñ„Ð¸Ð»ÑŒÐ¼Ð° ----
export const fetchMoviesbySearch = async input => {
  const response = await axios.get(
    'https://api.themoviedb.org/3/search/movie',
    {
      params: {
        query: input,
        api_key: KEY,
      },
    }
  );
  return response.data;
};

